.friendsList {
    list-style: none;
    width: 200px;
    margin: 0 auto;
    padding: 0;
}

.item {
    display: flex;
    height: 75px;
    width: 100%;
    margin: 13px auto;
    border: 1px solid grey;
    box-shadow: 0px 3px 7px 0px grey;
}

.status {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 10px;
}

.online {
    display: flex;
    width: 12px;
    height: 12px;
    background-color: green;
    border-radius: 50%;
    box-shadow: 0px 0px 2.5px 0px green;
}

.offline {
    display: flex;
    width: 12px;
    height: 12px;
    background-color: red;
    border-radius: 50%;
    box-shadow: 0px 0px 1.5px 0px red;
}

.avatar {
    display: flex;
    align-content: center;
    box-shadow: inset 0px 0px 10px 0px lightgrey;
    margin-top: 10px;
    height: 75%;
}

.name {
    margin: 25px 15px;
    font-size: 22px;
    font-weight: 300;
}